  import {
    mapState,
    mapMutations
  } from 'vuex'
  import uniNavBar from "@/components/uni-nav-bar/uni-nav-bar.vue"
  const statusBarHeight = uni.getSystemInfoSync().statusBarHeight
  // textarea高度就是100vw-上面两个加起来，再加个透明背景图片
  const navBarHeight = 44
	export default {
    components: {
      uniNavBar
    },
		data() {
      const totalHeight = statusBarHeight + navBarHeight
			return {
				todo: null,
        textValue: '',
        type: '',
        minHeight: `calc(100vh - ${totalHeight}px)`,
        tabHeight: 0
			};
		},
    computed: {
      ...mapState(['currentMemo']),
    },
    methods: {
      ...mapMutations(['changeMemo']),
      deleteMemo() {
        uni.showModal({
          title: '确定删除备忘录吗',
          confirmText: '确定删除',
          cancelText: '取消删除',
          success: (res) => {
            if (res.confirm) {
              uni.$emit('deleteMemo', true)
              uni.$emit('changeMemo', false)
              uni.navigateBack()
              return
            }
            if (res.cancel) {
              return
            }
          }
        })
      },
      handleConfirm() {
        this.saveMemo()
      },
      back() {
        uni.showModal({
          title: '是否保存修改',
          confirmText: '保存修改',
          cancelText: this.type === 'edit' ? '取消修改' : '取消添加',
          success: (res) => {
            if (res.confirm) {
              // 写保存修改的代码
              this.saveMemo()
              return
            }
            if (res.cancel) {
              // 不保存的代码
              uni.$emit('changeMemo', false)
              uni.$emit('deleteMemo', false)
              uni.navigateBack()
              this.changeMemo('') // 这里是我改过的
              return
            }
          }
        })
      },
      saveMemo() {
        if (this.textValue === '') {
          uni.showToast({
            title: '内容不可以为空',
            icon: 'none'
          })
          return
        }
        this.changeMemo(this.textValue)
        if (this.type === 'edit') {
          uni.$emit('changeMemo', true)
          uni.$emit('deleteMemo', false)
        } else if (this.type === 'add') {
          uni.$emit('addMemo', true)
          uni.$emit('changeMemo', false)
          uni.$emit('deleteMemo', false)
        }
        uni.navigateBack()
      }
    },
    onLoad(option) {
      this.type = option.type // edit/add
      this.tabHeight = option.tabHeight
      this.textValue = this.currentMemo
    }
	}
